/*----------------------------------------------------------

BREAKPOINT MIXINS

-----------------------------------------------------------*/
html:before, body:before {
    display:none;
    content: "mobile";
}
@media (min-width: $(tablet)) {
    html:before, body:before {
        content:"tablet";
    }
}
@media (min-width: $(desktop)) {
    html:before, body:before {
        content:"desktop";
    }
}
.desktop{
    display:none;
}
@media (min-width: $(tablet)) {
    .desktop {
        display:none;
    }
}
@media (min-width: $(desktop)) {
    .desktop {
        display:block;
    }
}
.tablet{
    display:none;

}
@media (min-width: $(tablet)) {
    .tablet {
        display:block;
    }
}
.mobile{
    display:block;
}
@media (min-width: $(tablet)) {
    .mobile {
        display:none;
    }
}
.portables {
    display:block;
}
@media (min-width: $(desktop)) {
    .portables {
        display:none;
    }
}
